import * as React from "react";
import '@/css/backups.css';
import { observer, inject } from 'mobx-react';
interface IProps {
	match?: any,
	location?: object,
	history?: any,
	clickFrame: any,
	clickNext: any,
	frameFlag: boolean,
	helpWordFlag: boolean,
	firstFrame: any,
	noClickNext: any
}
interface IState {
	hlepList: object,
	playList: object,
	activeIndexArr: object
}
@inject((stores: any) => {
	// console.log(stores);
	return {
		clickFrame: stores.rootStore.actions.clickFrame,
		clickNext: stores.rootStore.actions.clickNext,
		frameFlag: stores.rootStore.store.frameFlag,
		helpWordFlag: stores.rootStore.store.helpWordFlag,
		firstFrame: stores.rootStore.actions.firstFrame,
		noClickNext: stores.rootStore.actions.noClickNext
	}
})
@observer
class Backups extends React.Component<IProps, IState> {
	public state = {
		hlepList: [],
		playList: ['eggA', 'eggB', 'eggC', 'eggD', 'eggE', 'eggF', 'eggG', 'eggH', 'eggJ', 'eggK', 'eggL', 'eggM'],
		activeIndexArr: {}
	}
	public componentDidMount() {
		document.title = '-';
		this.props.firstFrame();
		this.props.noClickNext();
		//
	}
	public componentWillUnmount() {
		document.title = '';
	}
	nextStepClick = (helpWordFlag, clickNext): any => {
		const { playList } = this.state;
		if (helpWordFlag) {
			this.setState({
				hlepList: ['eggA', 'eggB', 'eggC', 'eggD', 'eggE', 'eggF', 'eggG', 'eggH', 'eggJ', 'eggK', 'eggL', 'eggM'],
				playList: []
			})
			clickNext();
		} else {//
			playList.length === 12 ? this.props.history.push('/main') : '';
		}
	}
	chanceWordsClick = (index, res): any => {
		const { activeIndexArr, playList, hlepList } = this.state;
		if (activeIndexArr[index] === index) { delete activeIndexArr[index]; }
		else activeIndexArr[index] = index
		hlepList.filter((r, i) => {
			if (playList.length < 12) {
				activeIndexArr[index] === i ? playList.push(r) : "";
			}
		})
		//index
		playList.map((e, n) => {
			if (activeIndexArr[index] === undefined) if (res === e) playList.splice(n, 1);
		});
		this.setState({
			activeIndexArr,
			playList
		})
	}
	public render() {
		const {
			clickFrame: clickFrame,
			clickNext: clickNext,
			frameFlag: frameFlag,
			helpWordFlag: helpWordFlag
		} = this.props;

		let { hlepList, playList, activeIndexArr } = this.state
		return (
			<div className="backups-total">
				<div className='restore-account'></div>
				<div className="help-reword">
					<header className='reword-head'></header>
					<section className='reword-warning'>.</section>
					<div className='reword-words'>
						<ul className='words-sort'>

							{
								playList && playList.map((res, index) => {
									return (<li key={index}>{res}</li>)
								})
							}
						</ul>
					</div>
					{!helpWordFlag ? (<div className='chance-words-list'>
						<ul className='words-sort'>
							{
								hlepList && hlepList.map((res, index) => {
									return (<li key={index} onClick={this.chanceWordsClick.bind(this, index, res)} className={activeIndexArr[index] === index ? "listActive" : ""}>{res}</li>)
								})
							}
						</ul>
					</div>) : ""}

					<div className={helpWordFlag ? "reword-btn " : 'reword-btn confirm-btn'} onClick={this.nextStepClick.bind(this, helpWordFlag, clickNext)} style={{ backgroundColor: playList.length === 12 ? "rgba(18,169,237,1)" : "" }}>{helpWordFlag ? "" : ""}</div>
				</div>
				{
					frameFlag ? (<div className="modal-content" >
						<section className='modal-frame'>
							<div className='modal-head-img'></div>
							<p className="modal-no-screenshot"></p>
							<p className="modal-no-tip"></p>
							<div className="modal-btn" onClick={clickFrame}></div>
						</section>
					</div>) : ""
				}

			</div>
		)
	}
}


export default Backups;