
import {Store} from './store'
import {Actions} from './actions'

// 
const store = new Store();
//  actions store 
const actions = new Actions({store});


//stores
 class ActionsRoot{
	  public store:any;
	  public actions:any;
	 constructor() {
	 	this.store = store;	
		this.actions = actions;
	 }
 }
 const actionsRoot = new ActionsRoot();
export {actionsRoot} 