import {action} from 'mobx';
//
class Actions{
  public store:any;
  constructor({store}) {
		this.store = store;
  }
  //   action decorator 
  @action
  incA = () => {
    this.store.a++;
  }
  @action
  decA = () => {
    this.store.a--;
  }
  //
  @action
  clickFrame = () => {
    this.store.frameFlag = false;
  }
  @action
  firstFrame = () => {
    this.store.frameFlag = true;
  }
  //
   @action
  clickNext = () => {
    this.store.helpWordFlag = false;
  }
  @action
  noClickNext = ()=>{
	   this.store.helpWordFlag = true;
  }
}
export{
	Actions
}